or-theme.scss\";\n\ndialog {\n  border: 1px solid rgba($darker-main, 0.3);\n  box-shadow: 0 2px 10px rgba($darker-main, 0.5);\n  border-radius: 10px;\n  gap: 15px;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  padding: 30px;\n  margin: auto;\n  margin-top: 20svh;\n\n  .title {\n    font-size: 1rem;\n    margin-bottom: 15px;\n    text-align: center;\n  }\n\n  form {\n    /* min-width: 200px; */\n    background-color: lighten($light-contentbody, 5%);\n    opacity: 100%;\n    flex-grow: 1;\n    max-width: 300px;\n\n    display: grid;\n    grid-template-rows: 1fr 1fr;\n    grid-template-columns: 1fr 1fr;\n    gap: 15px;\n\n    input {\n      align-self: stretch;\n      border: 1px solid rgba($darker-main, 1);\n      border-radius: 10px;\n      padding: 5px;\n      grid-column: span 2;\n    }\n\n    .description {\n      align-self: center;\n      padding: 5px;\n      font-size: 0.8rem;\n      text-align: center;\n    }\n\n    & > div {\n      display: flex;\n      gap: 10px;\n      justify-content: flex-end;\n      align-self: stretch;\n    }\n    button {\n      width: 100px;\n      padding: 5px;\n      background-color: lighten($light-contentbody, 5%);\n      border-radius: 5px;\n\n      &:first-child {\n        border: 1px solid rgba($darker-main, 0.3);\n        &:active {\n          background-color: desaturate($light-contentbody, 4%);\n        }\n      }\n\n      &:nth-child(2) {\n        background-color: rgba($darker-main, 0.8);\n        color: rgba(white, 0.9);\n        &:active {\n          background-color: grayscale(rgba($darker-main, 0.5));\n        }\n        &:disabled {\n          background-color: rgba($darker-main, 0.5);\n          color: rgba(white, 0.3);\n        }\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
