is;\n  }\n\n  input:checked + .name {\n    text-decoration: line-through;\n    color: lighten(gray, 20%);\n  }\n}\n\n.todo[data-priority=\"high\"] {\n  box-shadow: inset 3px 0px red;\n}\n.todo[data-priority=\"medium\"] {\n  box-shadow: inset 3px 0px goldenrod;\n}\n.todo[data-priority=\"low\"] {\n  box-shadow: inset 3px 0px green;\n}\n.todo:has(input:checked) {\n  box-shadow: none;\n}\n","//https://colorhunt.co/palette/f5efe6e8dfcaaebdca7895b2\n\n// original color scheme;\n// $background: rgb(245, 239, 230);\n// $light-contentbody: rgb(232, 223, 202);\n// $light-main:rgb(174, 189, 202);\n// $darker-main: rgb(120, 149, 178);\n// $input: darken( rgb(232, 223, 202), 10%);\n\n\n// NUDE THEME\n$background: #E4D0D0;\n$light-contentbody:#F5EBEB;\n$light-main: hsl(0, 28%, 77%);\n$darker-main: #867070;\n$input: lighten($light-main, 10%);"],"sourceRoot":""}]);
